  top: 34%;
  left: 30%;
  display: block;
  border-radius: 4px;
  border-top: 3px solid white;
  border-right: 3px solid white;
  width: 10px;
  height: 10px;
  transform: rotate(45deg);
}

.slider-container__btn_prev {
  height: 47px;
  width: 47px;
  border: 1px solid #FDFDFD;
}

.slider-container__btn_prev:before {
  content: '';
  position: absolute;
  top: 34%;
  left: 38%;
  display: block;
  border-radius: 4px;
  border-bottom: 3px solid white;
  border-left: 3px solid white;
  width: 10px;
  height: 10px;
  transform: rotate(45deg);
}

